import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { Button, Form, Input, Modal, Select, Alert } from "antd";
import { CheckCircleOutlined } from "@ant-design/icons";
import "./Profile.less";
import { country, phoneCode, captcha, emailCode } from "../../api/common";
import {
  getAccount,
  updateAccount,
  verifiedPhone,
  updatePhone,
  verifiedEmail,
  updateEmail,
  updatePassword,
} from "../../api/account";
import { showDialog } from "../../utils/util";

export default function Profile() {
  const navigate = useNavigate();
  const [formLayout, setFormLayout] = useState("horizontal");
  const { Option } = Select;
  const [levelNum, setLevelNum] = useState(0);
  const [level, setLevel] = useState([
    {
      text: "低",
      textColor: "#F64E60",
      color1: "#F64E60",
      color2: "#DBDBDD",
      color3: "#DBDBDD",
    },
    {
      text: "中",
      textColor: "#1BC5BD",
      color1: "#3699FF",
      color2: "#3699FF",
      color3: "#DBDBDD",
    },
    {
      text: "高",
      textColor: "#1BC5BD",
      color1: "#1BC5BD",
      color2: "#1BC5BD",
      color3: "#1BC5BD",
    },
  ]);

  // 图形验证码
  const [phoneCaptcha, setPhoneCaptcha] = useState({
    captcha: "",
    token: "",
  });

  // 错误信息提示
  const [passErr, setPassErr] = useState("");

  // form表单数据
  const [accountForm] = Form.useForm();
  const [phoneForm] = Form.useForm();
  const [rePhoneForm] = Form.useForm();
  const [emailForm] = Form.useForm();
  const [reEmailForm] = Form.useForm();
  const [passForm] = Form.useForm();

  // 按钮倒计时
  const [phoneFlag, setPhoneFlag] = useState(true);
  let [phoneCountDown, setPhoneCountDown] = useState(60);

  const [rePhoneFlag, setRePhoneFlag] = useState(true);
  let [rePhoneCountDown, setRePhoneCountDown] = useState(60);

  const [emailFlag, setEmailFlag] = useState(true);
  let [emailCountDown, setEmailCountDown] = useState(60);

  const [reEmailFlag, setReEmailFlag] = useState(true);
  let [reEmailCountDown, setReEmailCountDown] = useState(60);

  // 用户信息
  const [userAccount, setUserAccount] = useState();
  // 国家列表
  const [countryList, setCountryList] = useState([]);
  // 控制展示
  const [phoneModalVisible, setPhoneModalVisible] = useState(false);
  const [rePhoneModalVisible, setRePhoneModalVisible] = useState(false);
  const [emailModalVisible, setEmailModalVisible] = useState(false);
  const [reEmailModalVisible, setReEmailModalVisible] = useState(false);
  const [passModalVisible, setPassModalVisible] = useState(false);

  // 通用信息
  const [commonData,setCommonData] = useState()

  // 页面主表单
  const formItemLayout =
    formLayout === "horizontal"
      ? {
        labelCol: {
          span: 6,
        },
        wrapperCol: {
          span: 14,
        },
        extra: {
          span: 4,
        },
      }
      : null;
  const msgItemLayout =
    formLayout === "horizontal"
      ? {
        wrapperCol: {
          span: 14,
          offset: 6,
        },
      }
      : null;
  const buttonItemLayout =
    formLayout === "horizontal"
      ? {
        wrapperCol: {
          span: 4,
          offset: 10,
        },
      }
      : null;

  // 修改弹窗 modal
  const modalFormItemLayout =
    formLayout === "horizontal"
      ? {
        labelCol: {
          span: 4,
        },
        wrapperCol: {
          span: 20,
        },
      }
      : null;

  useEffect(() => {
    getUserAccount();
    getCountry()
  }, []);

  // 获取国家
  const getCountry = () => {
    country().then((res) => {
      if (res.data.status === 200) {
        setCountryList(res.data.data.list);
      }
    });

  };

  // 获取账户信息
  const getUserAccount = () => {
    getAccount().then((res) => {
      if (res.data.status === 200) {
        let account = res.data.data.account

        // 设置为 undefined select 的 placeholder 才会显示
        if (!account.country) {
          account.country = undefined
        }
        if (!account.language) {
          account.language = undefined
        }
        setUserAccount(account);
        // accountForm.setFieldsValue(userAccount);
        let num = 0;
        if (account.phone) {
          num += 1;
          setLevelNum(num);
          console.log("phone");
        }
        if (account.email) {
          num += 1;
          setLevelNum(num);
        }
      }
    });
  };

  // 保存账户信息
  const editAccount = () => {
    const formData = accountForm.getFieldsValue();
    updateAccount(formData).then((res) => {
      if (res.data.status === 200) showDialog("保存账户信息成功！", "success");
    });
  };

  // 修改新手机号
  const [phoneParams, setPhoneParams] = useState({
    phone_code: "86",
    phone: "",
    code: "",
  })

  // 区号
  const selectcountry = (e) => {
    setPhoneParams({ ...phoneParams, phone_code: e });
  };

  // 新手机号
  const getphonevalue = (e) => {
    setPhoneParams({
      ...phoneParams,
      phone: e.target.value,
    });
  };

  // 新手机号验证码
  const ChangecodeValue = (e) => {
    setPhoneParams({
      ...phoneParams,
      code: e.target.value,
    });
  }

  // 验证手机号码
  const verPhone = () => {
    let isPass = true;
    const formData = phoneForm.getFieldsValue();
    if (formData.code === undefined || formData.code === "") {
      setPassErr("请输入六位验证码");
      isPass = false;
    } else if (formData.code.length !== 6) {
      setPassErr("请检查验证码位数");
      isPass = false;
    }
    let code = formData.code.toString();
    if (isPass) {
      setPassErr("");
      verifiedPhone({ code }).then((res) => {
        if (res.data.status === 200) {
          showDialog("原手机验证成功，请修改您的手机号", "success");
          setPhoneParams({
            ...phoneParams,
            phone: null,
            code: null
          })
          setPhoneModalVisible(false);
          setRePhoneModalVisible(true);
        }
      });
    }
  };

  // 更换手机号码
  const editPhone = () => {
    let isPass = true;
    console.log(phoneParams);
    if (phoneParams.phone === undefined || phoneParams.phone === "") {
      setPassErr("请输入手机号");
      isPass = false;
    }

    if (phoneParams.code === undefined || phoneParams.code === "") {
      setPassErr("请输入六位验证码");
      isPass = false;
    } else if (phoneParams.code.length !== 6) {
      setPassErr("请检查验证码位数");
      isPass = false;
    }

    if (isPass) {
      setPassErr("");
      console.log(phoneParams);
      updatePhone(phoneParams).then((res) => {
        if (res.data.status === 200) {
          showDialog("恭喜您，手机号修改成功", "success");
          userAccount.phone = phoneParams.phone;
          setUserAccount(userAccount);
          setRePhoneModalVisible(false);
          if (userAccount.phone === undefined || userAccount.phone === "") {
            let num = levelNum = 1
            setLevelNum(num)
          }
        }
      });
    }
  };

  // 验证邮箱
  const verEmail = () => {
    const formData = emailForm.getFieldsValue();
    let isPass = true;
    if (formData.code === undefined || formData.code === "") {
      setPassErr("请输入六位邮箱验证码");
      isPass = false;
    } else {
      if (formData.code.length !== 6) {
        setPassErr("验证码应为6位");
        isPass = false;
      }
    }
    if (isPass) {
      setPassErr("");
      verifiedEmail({ code: formData.code }).then((res) => {
        if (res.data.status === 200) {
          setEmailModalVisible(false);
          showDialog("验证成功，请更改您的邮箱", "success");
          showReEmailModal();
        }
      });
    }
  };

  // 更改邮箱
  const editEmail = () => {
    const formData = reEmailForm.getFieldsValue();
    setPassErr("")
    let isPass = true;
    if (formData.email === undefined || formData.email === "") {
      setPassErr("请输入邮箱");
      isPass = false;
    } else {
      const emailRegExp = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (!emailRegExp.test(formData.email)) {
        setPassErr("邮箱格式不正确");
        isPass = false;
      }
    }

    if (formData.code === undefined || formData.code === "") {
      setPassErr("请输入验证码");
      isPass = false;
    } else {
      if (formData.code.length !== 6) {
        setPassErr("验证码为6位");
        isPass = false;
      }
    }

    if (isPass) {
      setPassErr("");
      updateEmail(formData).then((res) => {
        if (res.data.status === 200) {
          userAccount.email = formData.email;
          setUserAccount(userAccount);
          showDialog("修改邮箱成功", "success");
          if (userAccount.email === undefined || userAccount.email === "") {
            let num = levelNum = 1
            setLevelNum(num)
          }
          setReEmailModalVisible(false)
        }
      });
    }
  };

  // 更改密码
  const editPass = () => {
    let isPass = true;
    const formData = passForm.getFieldsValue();
    if (formData.old_password === undefined || formData.old_password === "") {
      setPassErr("请输入当前密码");
      isPass = false;
    } else {
      if (
        formData.old_password.length < 6 ||
        formData.old_password.length > 32
      ) {
        setPassErr("密码格式错误，需为6~32位的字符");
        isPass = false;
      }
    }

    if (formData.new_password === undefined || formData.new_password === "") {
      setPassErr("请输入新密码");
      isPass = false;
    } else {
      if (
        formData.new_password.length < 6 ||
        formData.new_password.length > 32
      ) {
        setPassErr("新密码格式错误，需为6~32位的字符");
        isPass = false;
      }
    }

    if (formData.repassword === undefined || formData.repassword === "") {
      setPassErr("请输入验证密码");
      isPass = false;
    } else {
      if (formData.repassword.length < 6 || formData.repassword.length > 32) {
        setPassErr("验证密码格式错误，需为6~32位的字符");
        isPass = false;
      }
      if (formData.repassword !== formData.new_password) {
        setPassErr("两次密码输入不一致");
        isPass = false;
      }
    }
    if (isPass) {
      setPassErr("");
      updatePassword(formData).then((res) => {
        if (res.data.status === 200) {
          showDialog("密码更改成功！请重新登录", "success");
          navigate("/login");
        }
      });
    }
  };

  // 验证原手机倒计时
  let phoneTimer = null;
  const countDown = () => {
    setPhoneFlag(false);
    setPhoneCountDown(--phoneCountDown);
    phoneTimer = setInterval(() => {
      if (phoneCountDown > 1) {
        setPhoneFlag(false);
        setPhoneCountDown(--phoneCountDown);
      } else {
        clearInterval(phoneTimer);
        phoneTimer = null;
        setPhoneFlag(true);
        setPhoneCountDown(60);
      }
    }, 1000);
  };

  // 验证原邮箱倒计时
  let emailTimer = null;
  const doEmailCountDown = () => {
    setEmailFlag(false);
    setEmailCountDown(--emailCountDown);
    emailTimer = setInterval(() => {
      if (emailCountDown > 1) {
        setEmailFlag(false);
        setEmailCountDown(--emailCountDown);
      } else {
        clearInterval(emailTimer);
        emailTimer = null;
        setEmailFlag(true);
        setEmailCountDown(60);
      }
    }, 1000);
  };

  // 修改邮箱倒计时
  let reEmailTimer = null;
  const doReEmailCountDown = () => {
    setReEmailFlag(false);
    setReEmailCountDown(--reEmailCountDown);
    reEmailTimer = setInterval(() => {
      if (reEmailCountDown > 1) {
        setReEmailFlag(false);
        setReEmailCountDown(--reEmailCountDown);
      } else {
        clearInterval(reEmailTimer);
        reEmailTimer = null;
        setReEmailFlag(true);
        setReEmailCountDown(60);
      }
    }, 1000);
  };

  // 修改手机倒计时
  let rePhoneTimer = null;
  const reCountDown = () => {
    setRePhoneFlag(false);
    setRePhoneCountDown(--rePhoneCountDown);
    rePhoneTimer = setInterval(() => {
      if (rePhoneCountDown > 1) {
        setRePhoneFlag(false);
        setRePhoneCountDown(--rePhoneCountDown);
      } else {
        clearInterval(rePhoneTimer);
        rePhoneTimer = null;
        setRePhoneFlag(true);
        setRePhoneCountDown(60);
      }
    }, 1000);
  };

  // 获取原手机验证码
  const getPhoneCode = () => {
    const formData = phoneForm.getFieldValue();
    const params = {
      action: "verify",
      phone: userAccount.phone,
      phone_code: userAccount.phone_code,
      token: phoneCaptcha.token,
      captcha: formData.captcha,
    };
    phoneCode(params).then((res) => {
      if (res.data.status === 200) {
        // 发送成功显示60s内不再发送
        countDown();
      }
    });
  };
  // 获取新手机验证码
  const getRePhoneCode = () => {
    let isPass = true

    if (phoneParams.phone === undefined || phoneParams.phone === "") {
      isPass = false
      setPassErr("请输入新手机号")
    }
    const params = {
      action: "update",
      phone: phoneParams.phone,
      phone_code: phoneParams.phone_code,
      token: "",
      captcha: "",
    };

    if (isPass) {
      setPassErr("")
      phoneCode(params).then((res) => {
        if (res.data.status === 200) {
          // 改变发送验证码按钮状态
          reCountDown();
        }
      });
    }

  };

  // 显示验证原手机 modal
  const showEditPhoneModal = () => {
    setPassErr("");
    setPhoneModalVisible(true);
  };

  const showPhoneModal = () => {
    setPassErr("");
    setRePhoneModalVisible(true)
  }


  // 显示更改邮箱 modal
  const showReEmailModal = () => {
    setPassErr("");
    setReEmailModalVisible(true);
  };

  // 显示 验证原邮箱 modal
  const showEmailModal = () => {
    setPassErr("");
    setEmailModalVisible(true);
  };

  // 获取图形验证码
  const getCaptcha = () => {
    captcha().then((res) => {
      if (res.data.status === 200) {
        setPhoneCaptcha(res.data.data);
      }
    });
  };

  // 显示更改密码弹窗
  const showEditPassModal = () => {
    setPassModalVisible(true);
    setPassErr("");
  };

  // 获取邮箱验证码
  const getEmailCode = (action) => {
    let formData = {};
    if (action === "verify") {
      formData = emailForm.getFieldValue();
    } else if (action === "update") {
      formData = reEmailForm.getFieldValue();
    }
    const params = {
      action,
      email: formData.email,
      token: phoneCaptcha.token,
      captcha: formData ? formData.code : "",
    };
    emailCode(params).then((res) => {
      if (res.data.status === 200) {
        if (action === "verify") {
          // 验证邮箱
          doEmailCountDown();
        } else if (action === "update") {
          // 修改邮箱
          doReEmailCountDown();
        }
      }
    });
  };

  return (
    <div className="profile com-card">
      {
        userAccount ?
          <>
            <div className="card-title">概要</div>
            {/* 提示信息结束 */}
            {/* 表单开始 */}
            <div className="card-form">

              <Form
                {...formItemLayout}
                layout={formLayout}
                form={accountForm}
                colon={false}
                size={"large"}
                onFinish={editAccount}
                initialValues={
                  userAccount
                }
              >
                <Form.Item {...msgItemLayout}>
                  <div className="card-msg">
                    <div className="card-msg-item">
                      登录账号：建议您
                      <span className="msg-item-strong">完善账户信息</span>
                      ，以便工作人员给您提供更好的服务！
                    </div>
                    <div className="card-msg-item">
                      安全等级：
                      <span className="item-remind">
                        <span style={{ color: level[levelNum].textColor }}>
                          {level[levelNum].text}
                        </span>
                        <span
                          className="item-remind-block"
                          style={{ background: level[levelNum].color1 }}
                        />
                        <span
                          className="item-remind-block"
                          style={{ background: level[levelNum].color2 }}
                        />
                        <span
                          className="item-remind-block"
                          style={{ background: level[levelNum].color3 }}
                        />
                      </span>
                      建议您启动全部安全设置，以保障账户安全。
                    </div>
                  </div>
                </Form.Item>
                <Form.Item
                  label="姓名"
                  name="username"
                >
                  <Input className="form-input" placeholder="请输入姓名" />
                </Form.Item>
                <Form.Item label="公司名称" name="company">
                  <Input className="form-input" placeholder="请输入公司名称" />
                </Form.Item>
                <Form.Item label="国家" name="country">
                  <Select
                    className="form-input"
                    showSearch={true}
                    placeholder="请选择国家"
                    filterOption={(input, option) => option.children.includes(input)}
                    filterSort={(optionA, optionB) =>
                      optionA.children
                        .toLowerCase()
                        .localeCompare(optionB.children.toLowerCase())
                    }
                  >
                    {countryList.map((item) => {
                      return (
                        <Option key={item.name} value={item.name}>
                          {item.name_zh}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
                <Form.Item label="地址" name="address">
                  <Input className="form-input" placeholder="请输入地址" />
                </Form.Item>
                <Form.Item label="语言" name="language">
                  <Select className="form-input" placeholder="请选择语言">
                    <Option value="zh-cn">中文</Option>
                    <Option value="en-us">英文</Option>
                  </Select>
                </Form.Item>
                <Form.Item
                  label="手机"
                  extra={
                    !userAccount.phone ? (
                      <span className="extra-text">
                        <div className="extra-icon">!</div>
                        绑定个人手机，提高账户安全等级
                      </span>
                    ) : null
                  }
                >
                  <Input
                    placeholder="请输入手机号"
                    disabled
                    value={`+${userAccount.phone_code} | ${userAccount.phone}`}
                  />
                  {userAccount.phone ? (
                    <Button
                      className="revise-button"
                      type="link"
                      onClick={() => showEditPhoneModal()}
                    >
                      修改
                    </Button>
                  ) : (
                    <Button
                      className="revise-button"
                      type="link"
                      onClick={() => showPhoneModal()}
                    >
                      绑定
                    </Button>
                  )}
                </Form.Item>
                <Form.Item
                  label="邮箱"
                  extra={
                    !userAccount.email ? (
                      <span className="extra-text">
                        <div className="extra-icon">!</div>
                        绑定个人邮箱，提高账户安全等级
                      </span>
                    ) : null
                  }
                >
                  <Input
                    placeholder="请绑定邮箱"
                    disabled
                    value={userAccount.email}
                  />
                  {userAccount.email ? (
                    <Button
                      className="revise-button"
                      type="link"
                      onClick={showEmailModal}
                    >
                      修改
                    </Button>
                  ) : (
                    <Button
                      className="revise-button"
                      type="link"
                      onClick={showReEmailModal}
                    >
                      绑定
                    </Button>
                  )}
                </Form.Item>
                <Form.Item
                  label="密码"
                  extra={
                    <span className="extra-text">
                      <div className="extra-icon">!</div>
                      建议您输入的密码、包含数字、字母和特殊符号在内，以保障密码不易盗取
                    </span>
                  }
                >
                  <Input
                    placeholder="请输入密码"
                    type="password"
                    value="******"
                    disabled
                  />
                  <Button
                    className="revise-button"
                    type="link"
                    onClick={showEditPassModal}
                  >
                    修改
                  </Button>
                </Form.Item>
                <Form.Item {...buttonItemLayout}>
                  <Button className="sub-button" type="primary" htmlType="submit">
                    保存
                  </Button>
                </Form.Item>
              </Form>


            </div>
            {/* 表单结束 */}

            {/* 验证原手机号modal */}
            <Modal
              visible={phoneModalVisible}
              footer={null}
              centered={true}
              closable={false}
              style={{ borderRadius: "10px", overflow: "auto" }}
              getContainer={false}
            >
              <div className="modal-phone">
                <div className="modal-title">
                  <div className="title-main-text">更改手机号</div>
                  <div className="title-vice-text">更改您的手机号码</div>
                </div>
                <div className="modal-form">
                  <Form
                    {...modalFormItemLayout}
                    layout={formLayout}
                    form={phoneForm}
                    initialvalues={{
                      layout: formLayout,
                    }}
                    colon={false}
                    size={"large"}
                    onFinish={verPhone}
                  >
                    <Form.Item noStyle>
                      {passErr ? (
                        <Alert
                          description={passErr}
                          type="error"
                          showIcon
                          style={{ color: "red", fontSize: "13px" }}
                        />
                      ) : (
                        <></>
                      )}
                    </Form.Item>
                    <Form.Item label="手机号" name="phone">
                      <Input
                        placeholder={"使用手机" + userAccount.phone + "验证"}
                        disabled
                      />
                    </Form.Item>
                    <Form.Item label="验证码" name="code">
                      <div className="itemCode">
                        <Input
                          className="modal-code-input"
                          placeholder="请输入六位短信验证码"
                        />
                        {phoneFlag ? (
                          <Button className="code-btn" onClick={getPhoneCode}>
                            验证码
                          </Button>
                        ) : (
                          <Button className="unactive" type="primary" disabled>
                            {phoneCountDown}重新发送
                          </Button>
                        )}
                      </div>
                    </Form.Item>
                    <Form.Item noStyle>
                      <div className="modal-footer">
                        <Button
                          className="close-button"
                          onClick={() => setPhoneModalVisible(false)}
                          type="primary"
                        >
                          取消
                        </Button>
                        <Button
                          className="sub-button"
                          type="primary"
                          htmlType="submit"
                        >
                          验证
                        </Button>
                      </div>
                    </Form.Item>
                  </Form>
                </div>
              </div>
            </Modal>

            {/* 更改新手机号 */}
            <Modal
              visible={rePhoneModalVisible}
              footer={null}
              centered={true}
              closable={false}
              style={{ borderRadius: "10px", overflow: "auto" }}
              getContainer={false}
              onCancel={() => setRePhoneModalVisible(false)}
            >
              <div className="modal-phone">
                <div className="modal-title">
                  <div className="title-main-text">{userAccount.phone?'更改手机号':'绑定手机号'}</div>
                  <div className="title-vice-text">{userAccount.phone?'更改您的手机号':'绑定您的手机号'}更改您的手机号码</div>
                </div>
                <div className="modal-form">
                  <div className="form-error">
                    {passErr ? (
                      <Alert
                        description={passErr}
                        type="error"
                        showIcon
                        style={{ color: "red", fontSize: "13px" }}
                      />
                    ) : (
                      <></>
                    )}
                  </div>
                  <div className="form-item">
                    <div className="item-label">新手机号</div>
                    <div className="select-input item-bg">
                      <Select
                        defaultValue="中国 +86"
                        onChange={selectcountry}
                      >
                        {countryList.map((item, index) => {
                          return (
                            <Option key={index} value={item.phone_code}>
                              {item.name_zh}+{item.phone_code}
                            </Option>
                          );
                        })}
                      </Select>
                      <Input
                        style={{
                          width: "100%",
                          backgroundColor: "#f3f3f9",
                          borderLeft: "1px solid #9696A3"
                        }}
                        bordered={false}
                        onChange={getphonevalue}
                        placeholder="请输入新手机号"
                      />
                    </div>
                  </div>
                  <div className="form-item">
                    <div className="item-label">验证码</div>
                    <div className="select-input">
                      <Input
                        className="modal-code-input"
                        placeholder="请输入六位短信验证码"
                        size="large"
                        style={{ border: "none" }}
                        onChange={ChangecodeValue}
                      />
                      {rePhoneFlag ? (
                        <Button className="code-btn" onClick={getRePhoneCode}>
                          验证码
                        </Button>
                      ) : (
                        <Button className="unactive" type="primary" disabled>
                          {rePhoneCountDown}重新发送
                        </Button>
                      )}
                    </div>
                  </div>
                  <div className="form-bottom">
                    <Button
                      className="close-button"
                      type="primary"
                      onClick={() => setRePhoneModalVisible(false)}
                    >
                      取消
                    </Button>
                    <Button
                      className="sub-button"
                      type="primary"
                      onClick={editPhone}
                    >
                      验证
                    </Button>
                  </div>
                </div>
              </div>
            </Modal>

            {/* 验证原邮箱 */}
            <Modal
              visible={emailModalVisible}
              footer={null}
              centered={true}
              closable={false}
              style={{ borderRadius: "10px", overflow: "auto" }}
              getContainer={false}
              onCancel={() => setEmailModalVisible(false)}
            >
              <div className="modal-phone">
                <div className="modal-title">
                  <div className="title-main-text">更改邮箱</div>
                  <div className="title-vice-text">更改您的邮箱</div>
                </div>
                <div className="modal-form">
                  <Form
                    {...modalFormItemLayout}
                    layout={formLayout}
                    form={emailForm}
                    initialvalues={{
                      layout: formLayout,
                    }}
                    colon={false}
                    onFinish={verEmail}
                    size={"large"}
                  >
                    <Form.Item noStyle>
                      {passErr ? (
                        <Alert
                          description={passErr}
                          type="error"
                          showIcon
                          style={{ color: "red", fontSize: "13px" }}
                        />
                      ) : (
                        <></>
                      )}
                    </Form.Item>
                    <Form.Item label="邮箱" name="email">
                      <Input
                        placeholder={`请使用邮箱${userAccount.email}验证`}
                        disabled
                      />
                    </Form.Item>
                    <Form.Item label="验证码" name="code">
                      <div className="itemCode">
                        <Input
                          className="modal-code-input"
                          placeholder="请输入六位邮箱验证码"
                        />
                        {emailFlag ? (
                          <Button
                            className="code-btn"
                            onClick={() => getEmailCode("verify")}
                          >
                            验证码
                          </Button>
                        ) : (
                          <Button className="unactive" type="primary" disabled>
                            {emailCountDown}重新发送
                          </Button>
                        )}
                      </div>
                    </Form.Item>
                    <Form.Item noStyle>
                      <div className="modal-footer">
                        <Button
                          className="close-button"
                          type="primary"
                          onClick={() => setEmailModalVisible(false)}
                        >
                          取消
                        </Button>
                        <Button
                          className="sub-button"
                          type="primary"
                          htmlType="submit"
                        >
                          验证
                        </Button>
                      </div>
                    </Form.Item>
                  </Form>
                </div>
              </div>
            </Modal>

            {/* 修改邮箱 modal */}
            <Modal
              visible={reEmailModalVisible}
              // visible={true}
              footer={null}
              centered={true}
              closable={false}
              style={{ borderRadius: "10px", overflow: "auto" }}
              getContainer={false}
              onCancel={() => setReEmailModalVisible(false)}
            >
              <div className="modal-phone">
                <div className="modal-title">
                  <div className="title-main-text">{userAccount.email ? '更改邮箱' : '绑定邮箱'}</div>
                  <div className="title-vice-text">{userAccount.email ? '更改您的邮箱' : '绑定您的邮箱'}</div>
                </div>
                <div className="modal-form">
                  <Form
                    {...modalFormItemLayout}
                    layout={formLayout}
                    form={reEmailForm}
                    initialvalues={{
                      layout: formLayout,
                    }}
                    colon={false}
                    size={"large"}
                    onFinish={editEmail}
                  >
                    <Form.Item noStyle>
                      {passErr ? (
                        <Alert
                          description={passErr}
                          type="error"
                          showIcon
                          style={{ color: "red", fontSize: "13px" }}
                        />
                      ) : null}
                    </Form.Item>
                    <Form.Item label="邮箱" name="email">
                      <Input placeholder="请输入您的邮箱" />
                    </Form.Item>
                    <Form.Item label="验证码" name="code">
                      <div className="itemCode">
                        <Input
                          className="modal-code-input"
                          placeholder="请输入六位邮箱验证码"
                        />
                        {reEmailFlag ? (
                          <Button
                            className="code-btn"
                            onClick={() => getEmailCode("update")}
                          >
                            验证码
                          </Button>
                        ) : (
                          <Button className="unactive" type="primary" disabled>
                            {reEmailCountDown}重新发送
                          </Button>
                        )}
                      </div>
                    </Form.Item>
                    <Form.Item noStyle>
                      <div className="modal-footer">
                        <Button
                          className="close-button"
                          type="primary"
                          onClick={() => setReEmailModalVisible(false)}
                        >
                          取消
                        </Button>
                        <Button
                          className="sub-button"
                          type="primary"
                          htmlType="submit"
                        >
                          验证
                        </Button>
                      </div>
                    </Form.Item>
                  </Form>
                </div>
              </div>
            </Modal>

            {/* 修改密码 modal */}
            <Modal
              visible={passModalVisible}
              footer={null}
              centered={true}
              closable={false}
              style={{ borderRadius: "10px", overflow: "auto" }}
              getContainer={false}
              onCancel={() => setPassModalVisible(false)}
            >
              <div className="modal-phone">
                <div className="modal-title">
                  <div className="title-main-text">更改密码</div>
                  <div className="title-vice-text">更改您的账户密码</div>
                </div>
                <div className="modal-form">
                  <Form
                    {...modalFormItemLayout}
                    layout={formLayout}
                    form={passForm}
                    initialvalues={{
                      layout: formLayout,
                    }}
                    colon={false}
                    onFinish={editPass}
                    size={"large"}
                  >
                    <Form.Item noStyle>
                      {passErr ? (
                        <Alert
                          description={passErr}
                          type="error"
                          showIcon
                          style={{ color: "red", fontSize: "13px" }}
                        />
                      ) : (
                        <></>
                      )}
                    </Form.Item>
                    <Form.Item label="当前密码">
                      <Form.Item name="old_password" style={{ marginBottom: "0px" }}>
                        <Input type="password" placeholder="当前密码" />
                      </Form.Item>
                      <a onClick={() => { navigate("/forget"); }}>忘记密码？</a>
                    </Form.Item>
                    <Form.Item label="新密码" name="new_password">
                      <Input type="password" placeholder="新密码" />
                    </Form.Item>
                    <Form.Item label="验证密码" name="repassword">
                      <Input type="password" placeholder="验证密码" />
                    </Form.Item>
                    <Form.Item noStyle>
                      <div className="modal-footer">
                        <Button
                          className="close-button"
                          type="primary"
                          onClick={() => setPassModalVisible(false)}
                        >
                          取消
                        </Button>
                        <Button
                          className="sub-button"
                          type="primary"
                          htmlType="submit"
                        >
                          保存更改
                        </Button>
                      </div>
                    </Form.Item>
                  </Form>
                </div>
              </div>
            </Modal>

            {/* 修改结果提醒 modal */}
            <Modal
              visible={false}
              footer={null}
              centered={true}
              closable={false}
              style={{ borderRadius: "10px", overflow: "auto" }}
              getContainer={false}
            >
              <div className="modif-remind">
                <div className="remind-img">
                  <CheckCircleOutlined
                    style={{ color: "#1BC5BD", fontSize: "64px" }}
                  />
                </div>
                <div className="remind-text">恭喜您,手机号修改成功</div>
              </div>
            </Modal>
          </>
          :
          null
      }
    </div>
  );
}
